<template>
  <div id="main-container">
    <table class="fixed">
      <caption>
        {{ $route.meta.title }}
      </caption>
      <thead>
      <tr>
        <th scope="col"></th>
        <th scope="col">갯수</th>
        <th scope="col">합계</th>
        <th scope="col">총계</th>
      </tr>
      </thead>
      <tbody>
      <tr class="hover-tr">
        <th scope="col">건프라</th>
        <td data-label="No"></td>
        <td data-label="No"></td>
        <td data-label="No"></td>
      </tr>
      <tr class="hover-tr">
        <th scope="col">비 건프라</th>
        <td data-label="No"></td>
        <td data-label="No"></td>
        <td data-label="No"></td>
      </tr>
      </tbody>
    </table>
    <table class="fixed">
      <thead>
      <tr>
        <th scope="col"></th>
        <th scope="col">갯수</th>
        <th scope="col">합계</th>
        <th scope="col">총계</th>
      </tr>
      </thead>
      <tbody>
      <tr class="hover-tr">
        <th scope="col">SD</th>
        <td data-label="No"></td>
        <td data-label="No"></td>
        <td data-label="No"></td>
      </tr>
      <tr class="hover-tr">
        <th scope="col">HG</th>
        <td data-label="No"></td>
        <td data-label="No"></td>
        <td data-label="No"></td>
      </tr>
      <tr class="hover-tr">
        <th scope="col">RG</th>
        <td data-label="No"></td>
        <td data-label="No"></td>
        <td data-label="No"></td>
      </tr>
      <tr class="hover-tr">
        <th scope="col">MG</th>
        <td data-label="No"></td>
        <td data-label="No"></td>
        <td data-label="No"></td>
      </tr>
      <tr class="hover-tr">
        <th scope="col">PG</th>
        <td data-label="No"></td>
        <td data-label="No"></td>
        <td data-label="No"></td>
      </tr>
      <tr class="hover-tr">
        <th scope="col">기타</th>
        <td data-label="No"></td>
        <td data-label="No"></td>
        <td data-label="No"></td>
      </tr>
      </tbody>
    </table>
    <div class="input-wrapper" style="margin-top: 10px;">
      <button
        @click="_routeGoBack"
        class="cancel__button">
        뒤로가기
      </button>
    </div>
  </div>
</template>

<script>
import { mapState } from 'pinia'
import { figureStore } from '@/store/figures'
import { usersStore } from '@/store/users'

export default {
  name: 'user-gunpla-monthly-detail',
  data () {
    return {
      monthlyFigureDetailObjectOut: {
        monthlyFigureTypeDetailObjectOut: {
          monthlyFigureTypeDetailOut: {
            figureType: { code: '', value: '' },
            figureCount: 0,
            figurePrice: 0
          },
          monthlyNoneFigureDetailOut: {
            figureType: { code: '', value: '' },
            figureCount: 0,
            figurePrice: 0
          }
        },
        monthlyFigureGradeDetailObjectOut: {
          monthlySdFigureGradeDetailOut: {
            figureGrade: { code: '', value: '' },
            figureCount: 0,
            figurePrice: 0
          },
          monthlyHgFigureGradeDetailOut: {
            figureGrade: { code: '', value: '' },
            figureCount: 0,
            figurePrice: 0
          },
          monthlyRgFigureGradeDetailOut: {
            figureGrade: { code: '', value: '' },
            figureCount: 0,
            figurePrice: 0
          },
          monthlyMgFigureGradeDetailOut: {
            figureGrade: { code: '', value: '' },
            figureCount: 0,
            figurePrice: 0
          },
          monthlyPgFigureGradeDetailOut: {
            figureGrade: { code: '', value: '' },
            figureCount: 0,
            figurePrice: 0
          },
          monthlyEtcFigureGradeDetailOut: {
            figureGrade: { code: '', value: '' },
            figureCount: 0,
            figurePrice: 0
          }
        }
      }
    }
  },
  computed: {
    ...mapState(usersStore, ['userCustomInfo']),
    ...mapState(figureStore, ['getFigureMonthlyParams'])
  },
  methods: {
    _routeGoBack () {
      this.$router.go(-1)
    }
  },
  mounted () {
    this.getMonthlyFigureInfoDetail(
      this.userCustomInfo.userOid,
      this.getFigureMonthlyParams.year,
      this.getFigureMonthlyParams.month
    ).then(res => {
      this.monthlyFigureDetailObjectOut = res
    })
  }
}
</script>
<style scoped lang="scss">
.grid-item {
  height: 160px !important;
}
.button__wrapper {
  margin-top: 30px;
  text-align: right;
}
</style>
